.form {
    padding: 24px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 90px;
    width: 320px;
    background-color: #7DD8DB;
    border: 1px solid #0F3F3C;
    border-radius: 4px;
    box-shadow: 0px 4px 4px 0px #00000040;
  }

  .title {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .label {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    gap: 6px;
    font-weight: 500;
  }

  .input {
    padding: 4px;
    border: 1px solid #0F3F3C;
    border-radius: 4px;
  }

  .button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    padding: 6px 16px;
    border-radius: 4px;
    border: none;
    background-color: #0F3F3C;
    color: #FFFFFF;
  }

  .button:hover {
    cursor: pointer;
  }

  .button:hover,
  .button:focus {
    background-color: #F07F2E;
    box-shadow: 0px 4px 4px 0px #00000040;
    ;
  }
  